* {
  margin:0;
  padding:0;
  box-sizing:border-box;
}
body {
  padding:30px 0;
  background: linear-gradient(
    to left,
    rgba(7, 27, 82, 1) 0%,
    rgba(0, 128, 128, 1) 100%
  );
}
a{
  color:#333;
  text-decoration: none;
}
input{
  display: flex;
  margin: 0 auto;
}
.App{
  text-align: center;
}
h1 {
  font-family: 'Bigelow Rules';
  font-size:72px;
  color: #0ccac4;
}